<template>
    <div class="body">
        <div class="weui-tab" style="height:44px; padding-top: 2px" id="t4">
            <div class="weui-tab-nav">
                <!--                <a class="weui-navbar__item weui-nav-green" :class="{activeClass: isActive ? 'active':''}"-->
                <!--                   v-for="item in groups" v-bind:key="item" @click="getItemSelect(item)">-->
                <!--                    {{item.name}}({{item.nums}})-->
                <!--                </a>-->
                <a class="weui-navbar__item weui-nav-green" :class="{activeClass: isActive ? 'active':''}"
                   @click="getItemSelect(0)">{{groups[0].name}}({{groups[0].nums}})</a>
                <a class="weui-navbar__item weui-nav-green" :class="{activeClass: isActive ? 'active':''}"
                   @click="getItemSelect(1)">{{groups[1].name}}({{groups[1].nums}})</a>
                <a class="weui-navbar__item weui-nav-green" :class="{activeClass: isActive ? 'active':''}"
                   @click="getItemSelect(2)">{{groups[2].name}}({{groups[2].nums}})</a>
                <a class="weui-navbar__item weui-nav-green" :class="{activeClass: isActive ? 'active':''}"
                   @click="getItemSelect(3)">{{groups[3].name}}({{groups[3].nums}})</a>
            </div>
        </div>
        <a v-for="(item, index) in devices" :key="index">
            <div class="weui-cell">
                <div class="weui-cell__hd" style="position: relative;margin-right: 10px;">
                    <img src="../../assets/images/carYellow.png" style="height: 48px; width: 25px;display: block">
                </div>
                <div class="weui-cell__bd">
                    <p style="font-size: 18px;">部门:{{item.deptName}}</p>
                    <p style="font-size: 13px;color: #888888;">分组:{{item.groupId}}</p>
                    <p style="font-size: 13px;color: #888888;">SN:{{item.sn}}</p>
                </div>
            </div>
        </a>
    </div>
</template>

<script>
    export default {
        name: "device",
        data() {
            return {
                groups: [
                    {name: "全部", nums: 20},
                    {name: "在线", nums: 10},
                    {name: "离线", nums: 5},
                    {name: "未用", nums: 5}
                ],
                isActive: false, // 0为默认选择第一个，-1为不选择
                devices: {},
                deviceNums: 20,
                carName: "红色小轿车",
                deviceID: 1234,
                time: "2019-09-04 16:43"
            }
        },
        methods: {
            // switchTab: function () {
            //     console.log("测试点击A");
            //     this.isActive = true;
            // },
            getItemSelect: function (item) {
                console.log(item);
                this.isActive = true;
                this.getAllDeviceList(item);
            },
            getAllDeviceList: function (flag) {
                //let url = 'http://47.97.209.9:8082/device/list';
                let url = 'http://39.100.138.248:8082/device/list';
                // post 方法接收三个参数：
                // 参数1： 要请求的URL地址
                // 参数2： 要发送的数据对象
                // 参数3： 指定post提交的编码类型为 application/x-www-form-urlencoded
                switch(flag)
                {
                    case 0:
                        // get 方式获取数据
                        this.$http.get(url, {emulateJSON: true}).then(res => {
                            console.log(res);
                            this.devices = res.body;
                        });
                        break;
                    case 1:
                        // get 方式获取数据
                        this.$http.get(url, {emulateJSON: true}).then(res => {
                            console.log(res);
                        });
                        break;
                    case 2:
                        // JSONP形式从服务器获取数据
                        this.$http.jsonp(url, {emulateJSON: true}).then(res => {
                            console.log(res);
                        });
                        break;
                    case 3:
                        // this.$http.get('https://www.baidu.com', {emulateJSON: true}).then(res => {
                        //     console.log(res);
                        // });
                        //post 方式获取数据
                        this.$http.post(url, {}, {emulateJSON: true}).then(res => {
                            console.log(res);
                        });
                        break;
                }
            }
        },
        // 页面加载之前，用created钩子函数-获取网页数据
        created: function () {
            console.log("created");
            this.getAllDeviceList(0);
        },
        mounted: function () {
            console.log("mounted")
        },
        handle: function () {
            console.log("handle")
        },
        ready: function () {
            console.log("ready")
        }
    }
</script>

<style lang="less" scoped>
    .body {
        width: 100%;
        height: 100%;
    }

    .active {
        background-color: green;
    }
</style>
