import VueRouter from 'vue-router'
import Login from './components/Login.vue'
import Register from './components/Register.vue'
import Wrapper from './components/contents/Wrapper.vue'
import MAP from './components/contents/Map.vue';
import Device from './components/contents/Device.vue';
import Message from './components/contents/Message.vue';
import User from "./components/contents/User.vue";
//创建路由对象
const router = new VueRouter({
    routes: [
        //{path: '/', redirect: '/login'},     //这个是设置默认路由
        {path:'/login', component: Login},      //登陆界面，首页
        {path:'/register', component: Register},
        {path:'/content', component: Wrapper},
        {
            path: '/content',
            component: Wrapper,
            children: [
                //{path: '/', redirect: '/map'},     //这个是设置默认路由
                {path: '/map', component: MAP},
                {path: '/device', component: Device},
                {path: '/message', component: Message},
                {path: '/user', component: User},
            ]
        },
    ],
    //覆盖默认路由高亮的类，默认的类叫做router-link-active
    linkActiveClass: 'weui-bar__item_on'
})

//把路由对象暴露出去
export default router
