<template>
    <div class="map-body">
        <baidu-map class="map-content" center="洛阳市" :zoom="zoom" @ready="handler">
            <bm-map-type :map-types="['BMAP_NORMAL_MAP', 'BMAP_SATELLITE_MAP']"
                         anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-map-type>
            <bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true"
                            :autoLocation="true"></bm-geolocation>
            <bm-navigation anchor="BMAP_ANCHOR_TOP_LEFT" type="BMAP_NAVIGATION_CONTROL_ZOOM"></bm-navigation>
            <!--<bm-scale anchor="BMAP_ANCHOR_BOTTOM_LEFT"></bm-scale>-->
            <!--<bm-panorama></bm-panorama>-->
            <!--插入菜单项-->
            <bm-context-menu>
                <bm-context-menu-item :callback="gotoBeijing" text="去北京"></bm-context-menu-item>
                <bm-context-menu-item :callback="gotoShanghai" text="去上海"></bm-context-menu-item>
            </bm-context-menu>
            <!--在BMarker中插入菜单项-->
            <bm-marker :position="{lng: 116.404, lat: 39.915}" :dragging="true">
                <bm-context-menu>
                    <bm-context-menu-item :callback="getPosition" text="获取坐标"></bm-context-menu-item>
                </bm-context-menu>
            </bm-marker>
            <!--路书功能-->
            <bm-driving start="智慧工厂" end="市民之家" @searchcomplete="handleSearchComplete" :panel="false"
                        :autoViewport="true"></bm-driving>
            <bml-lushu
                    @stop="reset"
                    :path="path"
                    :icon="icon"
                    :play="play"
                    :rotation="true">
            </bml-lushu>
        </baidu-map>
        <!--<input v-model.number="longitude" placeholder="longitude" type="number">-->
        <!--<input v-model.number="latitude" placeholder="latitude" type="number">-->
        <!--<h5>longitude is: {{longitude}}</h5>-->
        <!--<h5>latitude is: {{latitude}}</h5>-->
        <!--<input type="button" value="显示坐标" v-on:click="showPosition">-->
    </div>
</template>

<script>
    export default {
        data() {
            return {
                longitude: 0,
                latitude: 0,
                center: {lng: 0, lat: 0},
                zoom: 15,
                play: true,
                path: [],
                icon: {
                    url: 'http://api.map.baidu.com/library/LuShu/1.2/examples/car.png',
                    //url: '../assets/images/carBlue.png',
                    size: {width: 52, height: 26},
                    opts: {anchor: {width: 27, height: 13}}
                }
            }
        },
        methods: {
            handler({BMap, map}) {
                console.log(BMap, map)
                // this.center.lng = 116.404
                // this.center.lat = 39.915
                this.longitude = this.center.lng;
                this.latitude = this.center.lat;
                this.zoom = 5;
                // map.addControl(new BMap.OverviewMapControl());
            },
            reset() {
                this.play = false
            },
            handleSearchComplete(res) {
                this.path = res.getPlan(0).getRoute(0).getPath()
            },
            gotoBeijing(e) {
                console.log(e);
                this.center = {
                    lng: 116.404,
                    lat: 39.915
                };
            },
            gotoShanghai(e) {
                console.log(e);
                this.center = {
                    lng: 121.473354,
                    lat: 31.238413
                }
            },
            getPosition(e) {
                console.log(e);
                alert(`${e.point.lng} / ${e.point.lat}`)
            },
            showPosition(e) {
                console.log(e);
                this.longitude = this.center.lng;
                this.latitude = this.center.lat;
                this.play = true;
            },
            //地图页面生命周期函数
            load: function () {
                console.log("map load!");
            },
        }
    }
</script>

<style lang="less" scoped>
    .map-body {
        width: 100%;
        height: 100%;
    }
    .map-content {
        width: 100%;
        height: 100%;
    }
</style>
